import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import EnemyFinder from "../views/EnemyFinder.vue"
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/enemyfinder',
    name: "EnemyFinder",
    component: EnemyFinder
  }
]

const router = new VueRouter({
  routes
})

export default router
